export function isJsonRpcPayload(payload) {
    return (typeof payload === "object" &&
        "id" in payload &&
        "jsonrpc" in payload &&
        payload.jsonrpc === "2.0");
}
export function isJsonRpcRequest(payload) {
    return isJsonRpcPayload(payload) && "method" in payload;
}
export function isJsonRpcResponse(payload) {
    return isJsonRpcPayload(payload) && (isJsonRpcResult(payload) || isJsonRpcError(payload));
}
export function isJsonRpcResult(payload) {
    return "result" in payload;
}
export function isJsonRpcError(payload) {
    return "error" in payload;
}
import { RESERVED_ERROR_CODES, STANDARD_ERROR_MAP, DEFAULT_ERROR, INTERNAL_ERROR, SERVER_ERROR } from "../error/okx-connect.error";
class IncrementalRandomGenerator {
    constructor(bits) {
        this.i = 1;
        const typedArray = bits === 8 ? new Uint8Array(1) : bits === 16 ? new Uint16Array(1) : new Uint32Array(1);
        this.initialValue = crypto.getRandomValues(typedArray)[0];
    }
    getNextValue() {
        return this.initialValue + this.i++;
    }
}
const uint8Generator = new IncrementalRandomGenerator(8);
const uint16Generator = new IncrementalRandomGenerator(16);
export function payloadId() {
    const date = Date.now() * 1000;
    const extra = uint8Generator.getNextValue();
    return date + extra;
}
export function getBigIntRpcId() {
    const date = BigInt(Date.now()) * BigInt(1000000);
    const extra = BigInt(uint16Generator.getNextValue());
    return date + extra;
}
export function formatJsonRpcRequest(method, params, id) {
    return {
        id: id || payloadId(),
        jsonrpc: "2.0",
        method,
        params,
    };
}
export function formatJsonRpcResult(id, result) {
    return {
        id,
        jsonrpc: "2.0",
        result,
    };
}
export function formatJsonRpcError(id, error, data) {
    return {
        id,
        jsonrpc: "2.0",
        error: formatErrorMessage(error, data),
    };
}
export function isReservedErrorCode(code) {
    return RESERVED_ERROR_CODES.includes(code);
}
export function getError(type) {
    if (!Object.keys(STANDARD_ERROR_MAP).includes(type)) {
        return STANDARD_ERROR_MAP[DEFAULT_ERROR];
    }
    return STANDARD_ERROR_MAP[type];
}
export function getErrorByCode(code) {
    const match = Object.values(STANDARD_ERROR_MAP).find((e) => e.code === code);
    if (!match) {
        return STANDARD_ERROR_MAP[DEFAULT_ERROR];
    }
    return match;
}
export function formatErrorMessage(error, data) {
    if (typeof error === "undefined") {
        return getError(INTERNAL_ERROR);
    }
    if (typeof error === "string") {
        error = Object.assign(Object.assign({}, getError(SERVER_ERROR)), { message: error });
    }
    if (typeof data !== "undefined") {
        error.data = data;
    }
    if (isReservedErrorCode(error.code)) {
        error = getErrorByCode(error.code);
    }
    return error;
}
function getUrlProtocol(url) {
    const matches = url.match(new RegExp(/^\w+:/, "gi"));
    if (!matches || !matches.length)
        return;
    return matches[0];
}
function matchRegexProtocol(url, regex) {
    const protocol = getUrlProtocol(url);
    if (typeof protocol === "undefined")
        return false;
    return new RegExp(regex).test(protocol);
}
export function isHttpUrl(url) {
    return matchRegexProtocol(url, "^https?:");
}
export function isWsUrl(url) {
    return matchRegexProtocol(url, "^wss?:");
}
export function parseConnectionError(e, url, type) {
    return e.message.includes("getaddrinfo ENOTFOUND") || e.message.includes("connect ECONNREFUSED")
        ? new Error(`Unavailable ${type} RPC url at ${url}`)
        : e;
}
