import nacl from 'tweetnacl';
import { hexToByteArray, toHexString, concatUint8Arrays, splitToUint8Arrays, base64decode, base64encode } from '../utils/utils';
export class SessionCrypto {
    constructor(keyPair = undefined) {
        this.nonceLength = 24;
        this.sessionKeyPair = keyPair ? this.createKeypairFromString(keyPair) : this.createKeypair();
        console.log('create session keypair from keypair:', keyPair, {
            publicKey: toHexString(this.sessionKeyPair.publicKey),
            secretKey: toHexString(this.sessionKeyPair.secretKey)
        });
    }
    createKeypair() {
        return nacl.box.keyPair();
    }
    createKeypairFromString(keyPair) {
        return {
            publicKey: hexToByteArray(keyPair.publicKey),
            secretKey: hexToByteArray(keyPair.secretKey),
        };
    }
    createNonce() {
        return nacl.randomBytes(this.nonceLength);
    }
    get keyPair() {
        return this.sessionKeyPair;
    }
    // dapp 加密:
    // base64(hex（nonce+加密内容）)
    encrypt(message, receiverPublicKey) {
        const encodedMessage = new TextEncoder().encode(message);
        const nonce = this.createNonce();
        const encrypted = nacl.box(encodedMessage, nonce, hexToByteArray(receiverPublicKey), this.keyPair.secretKey);
        console.log('use nonce: ', nonce, encrypted);
        let concatInfo = concatUint8Arrays(nonce, encrypted);
        console.log('before hex: ', concatInfo);
        var hexResult = toHexString(concatInfo);
        console.log('before encode: ', hexResult);
        var encodedResult = new TextEncoder().encode(hexResult);
        console.log('before base64: ', encodedResult);
        console.log('send encrypt:', `Decryption error: \n message: ${message.toString()} \n receiver pubkey: ${receiverPublicKey.toString()} \n keypair pubkey: ${toHexString(this.keyPair.publicKey)} \n keypair secretkey: ${toHexString(this.keyPair.secretKey)}`);
        return base64encode(encodedResult, false);
    }
    // 钱包加密内容
    // 注意：没有中间hex的环节
    // base64（nonce+加密内容)
    encryptWallet(message, receiverPublicKey) {
        const encodedMessage = new TextEncoder().encode(message);
        const nonce = this.createNonce();
        const encrypted = nacl.box(encodedMessage, nonce, hexToByteArray(receiverPublicKey), this.keyPair.secretKey);
        console.log('use nonce: ', nonce, encrypted);
        let concatInfo = concatUint8Arrays(nonce, encrypted);
        console.log('before hex: ', concatInfo);
        return base64encode(concatInfo, false);
    }
    // dapp 解密
    // base64(split(nonce+加密内容))
    decrypt(message, senderPublicKey) {
        let base64Content = base64decode(message, true);
        let messageByteArray = base64Content.toUint8Array();
        console.log('after base64 decode uint8 array: ', messageByteArray, this.nonceLength);
        const [nonce, internalMessage] = splitToUint8Arrays(messageByteArray, this.nonceLength);
        console.log('parse nonce: ', nonce);
        const decrypted = nacl.box.open(internalMessage, nonce, hexToByteArray(senderPublicKey), this.keyPair.secretKey);
        if (!decrypted) {
            throw new Error(`Decryption error: \n message: ${message.toString()} \n sender pubkey: ${senderPublicKey.toString()} \n keypair pubkey: ${toHexString(this.keyPair.publicKey)} \n keypair secretkey: ${toHexString(this.keyPair.secretKey)}`);
        }
        return new TextDecoder().decode(decrypted);
    }
    // dapp 解密
    // base64(hextoByte(split(nonce+加密内容)))
    decryptWallet(message, senderPublicKey) {
        let base64Content = base64decode(message, true);
        let messageByteArray = hexToByteArray(base64Content.toString());
        console.log('after base64 decode uint8 array: ', messageByteArray, this.nonceLength);
        const [nonce, internalMessage] = splitToUint8Arrays(messageByteArray, this.nonceLength);
        console.log('parse nonce: ', nonce);
        const decrypted = nacl.box.open(internalMessage, nonce, hexToByteArray(senderPublicKey), this.keyPair.secretKey);
        if (!decrypted) {
            throw new Error(`Decryption error: \n message: ${message.toString()} \n sender pubkey: ${senderPublicKey.toString()} \n keypair pubkey: ${toHexString(this.keyPair.publicKey)} \n keypair secretkey: ${toHexString(this.keyPair.secretKey)}`);
        }
        return new TextDecoder().decode(decrypted);
    }
    stringifyKeypair() {
        return {
            publicKey: toHexString(this.keyPair.publicKey),
            secretKey: toHexString(this.keyPair.secretKey)
        };
    }
}
