import nacl from 'tweetnacl';
import naclutil from 'tweetnacl-util';
import { fromString } from 'uint8arrays/from-string';
import { toString } from "uint8arrays/to-string";
import { hash } from "@stablelib/sha256";
import { safeJsonParse, safeJsonStringify } from './safejson';
import { logDebug } from "./log";
const BASE16 = "base16";
export function hexToByteArray(hexString) {
    if (hexString.length % 2 !== 0) {
        throw new Error(`Cannot convert ${hexString} to bytesArray`);
    }
    const result = new Uint8Array(hexString.length / 2);
    for (let i = 0; i < hexString.length; i += 2) {
        result[i / 2] = parseInt(hexString.slice(i, i + 2), 16);
    }
    return result;
}
export function toHexString(byteArray) {
    let hexString = '';
    byteArray.forEach(byte => {
        hexString += ('0' + (byte & 0xff).toString(16)).slice(-2);
    });
    return hexString;
}
export function concatUint8Arrays(buffer1, buffer2) {
    const mergedArray = new Uint8Array(buffer1.length + buffer2.length);
    mergedArray.set(buffer1);
    mergedArray.set(buffer2, buffer1.length);
    return mergedArray;
}
export function splitToUint8Arrays(array, index) {
    if (index >= array.length) {
        throw new Error('Index is out of buffer');
    }
    const subArray1 = array.slice(0, index);
    const subArray2 = array.slice(index);
    return [subArray1, subArray2];
}
function encodeUint8Array(value, urlSafe) {
    const encoded = naclutil.encodeBase64(value);
    if (!urlSafe) {
        return encoded;
    }
    return encodeURIComponent(encoded);
}
function decodeToUint8Array(value, urlSafe) {
    if (urlSafe) {
        value = decodeURIComponent(value);
    }
    return naclutil.decodeBase64(value);
}
export function base64encode(value, urlSafe) {
    let uint8Array;
    if (value instanceof Uint8Array) {
        uint8Array = value;
    }
    else {
        if (typeof value !== 'string') {
            value = safeJsonStringify(value);
        }
        uint8Array = naclutil.decodeUTF8(value);
    }
    return encodeUint8Array(uint8Array, urlSafe);
}
export function base64decode(value, urlSafe) {
    const decodedUint8Array = decodeToUint8Array(value, urlSafe);
    return {
        toString() {
            return naclutil.encodeUTF8(decodedUint8Array);
        },
        toObject() {
            try {
                return safeJsonParse(naclutil.encodeUTF8(decodedUint8Array));
            }
            catch (e) {
                return null;
            }
        },
        toUint8Array() {
            return decodedUint8Array;
        }
    };
}
export function checkWSUrl(url) {
    const match = url.match(new RegExp(/^\w+:/, "gi"));
    if (!match || !match.length) {
        return false;
    }
    let scheme = match[0];
    if (scheme === undefined) {
        return false;
    }
    return new RegExp("^wss?:").test(scheme);
}
export const resolveWebSocketImplementation = () => {
    if (typeof WebSocket !== "undefined") {
        return WebSocket;
    }
    else if (typeof global !== "undefined" && typeof global.WebSocket !== "undefined") {
        return global.WebSocket;
    }
    else if (typeof window !== "undefined" && typeof window.WebSocket !== "undefined") {
        return window.WebSocket;
    }
    else if (typeof self !== "undefined" && typeof self.WebSocket !== "undefined") {
        return self.WebSocket;
    }
    return WebSocket;
    //
    // return require("ws");
};
export const hasBuiltInWebSocket = () => typeof WebSocket !== "undefined" ||
    (typeof global !== "undefined" && typeof global.WebSocket !== "undefined") ||
    (typeof window !== "undefined" && typeof window.WebSocket !== "undefined") ||
    (typeof self !== "undefined" && typeof self.WebSocket !== "undefined");
export const hasLocalStorage = () => (typeof global !== "undefined" && (typeof global.localStorage !== "undefined")) ||
    (typeof window !== "undefined" && typeof window.localStorage !== "undefined");
export const resolveLocalStorage = () => {
    if (typeof global !== "undefined" && typeof global.localStorage !== "undefined") {
        return global.localStorage;
    }
    else if (typeof window !== "undefined" && typeof window.localStorage !== "undefined") {
        return window.localStorage;
    }
    return localStorage;
};
export const isBrowser = () => typeof window !== "undefined";
export const RANDOM_LENGTH = 32;
export function hashKey(key, encode = BASE16) {
    logDebug(`hash key called ${key}`);
    const result = hash(fromString(key, encode));
    return toString(result, encode);
}
export function generateRandomBytes32() {
    return nacl.randomBytes(RANDOM_LENGTH);
}
export function generateTopic() {
    let randomBytes32 = generateRandomBytes32();
    logDebug(`generate topic called ${randomBytes32}`);
    return hashKey(toString(randomBytes32, BASE16));
}
export const isValidObject = (obj) => Object.getPrototypeOf(obj) === Object.prototype && Object.keys(obj).length;
export function isIos() {
    return /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
}
